




















































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { imageDataToUrl, imageDataFromFile } from '@/utils/image';
import { getFileExtension, getFileNameWithoutExtension, dataURItoBlob, download, CustomFile } from '@/utils/file';

import ConvertWorker from '@/assets/convert.worker.js';

interface Warning {
    displayText: string;
    description: string;
}

@Component
export default class FileItemVue extends Vue {
    @Prop({ type: File, required: true }) private inputFile!: File;

    private warning: null|Warning = null;
    private worker: ConvertWorker|null = null;
    private error: unknown|null = null;
    private result: CustomFile|null = null;

    private created () {
        this.convert().catch(e => { console.error(e); this.error = e; });
    }

    private destroyed () {
        if (this.worker !== null) this.worker.terminate();
    }

    @Watch('result')
    private emitResult () {
        this.$emit('result', this.result);
    }

    private download () {
        if (this.result === null) return;

        download(this.result);
    }

    private showError () {
        alert(`The following error occurred, while trying to convert your file:\n\n${this.error}\n\nWe would appreciate if you would help us make this tool even better and report the issue, by clicking the "Having Issues?"-Button in the lower right corner of the screen.`);
    }

    private showWarning () {
        if (this.warning === null) return;

        alert(this.warning.description);
    }

    private async convert () {
        if (this.worker === null) {
            this.worker = new ConvertWorker();
        }

        if (this.extension === 'paa') {
            // convert paa to png
            const data = await this.worker.convertPaaToImage(this.inputFile);

            const blob = dataURItoBlob(imageDataToUrl(data));

            this.result = { blob, name: this.newName };
        } else {
            const data = await imageDataFromFile(this.inputFile);

            // check wether both dimensions are powers of two
            if (Math.log2(data.width) % 1 !== 0 || Math.log2(data.height) % 1 !== 0) {
                this.warning = {
                    displayText: 'Dimensions have to<br/>be powers of two',
                    description: 'The dimensions (width and height) of PAA images have to be powers of two (2ⁿ).\nYour image has to fulfill the same conditions in order to convert it to PAA.'
                };
                return;
            }

            const blob = await this.worker.convertImageToPaa(data);

            this.result = { blob, name: this.newName };
        }

        // log google analytics
        // eslint-disable-next-line @typescript-eslint/camelcase
        this.$gtag.event('conversion', { event_category: 'conversion', non_interaction: true });

        this.worker.terminate();
    }

    private get extension () {
        return getFileExtension(this.inputFile);
    }

    private get newName () {
        const nameWithoutExtension = getFileNameWithoutExtension(this.inputFile);

        return `${nameWithoutExtension}.${this.extension === 'paa' ? 'png' : 'paa'}`;
    }

    private get state () {
        if (this.result !== null) return 'done';

        if (this.warning !== null) return 'warning';
        if (this.error !== null) return 'error';

        return 'loading';
    }
}
